.chat-container {
  display: flex;
  height: 100vh;
  background: #f5f6f8;
}

.sidebar {
  width: 280px;
  background: #fff;
  border-right: 1px solid #ddd;
  display: flex;
  flex-direction: column;
}

.sidebar-header {
  padding: 15px;
  background: #007bff;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.user-info span {
  margin-right: 10px;
  font-weight: 500;
}

.users-list {
  flex: 1;
  overflow-y: auto;
  padding: 10px;
}

.user-item {
  display: flex;
  align-items: center;
  padding: 8px;
  cursor: pointer;
  border-radius: 5px;
  transition: background 0.2s;
}

.user-item:hover,
.user-item.active {
  background: #e9f2ff;
}

.user-avatar {
  width: 35px;
  height: 35px;
  background: #007bff;
  color: #fff;
  border-radius: 50%;
  text-align: center;
  line-height: 35px;
  font-weight: bold;
  margin-right: 10px;
}

.user-status {
  font-size: 12px;
  color: gray;
}

.user-status.online {
  color: green;
}

.chat-area {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.chat-header {
  padding: 15px;
  background: #fff;
  border-bottom: 1px solid #ddd;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.messages-container {
  flex: 1;
  padding: 15px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.message {
  margin: 8px 0;
  max-width: 70%;
  padding: 10px;
  border-radius: 10px;
  position: relative;
}

.message.sent {
  background: #d1f0ff;
  align-self: flex-end;
}

.message.received {
  background: #fff;
  align-self: flex-start;
}

.message-content p {
  margin: 0;
}

.message-time {
  display: block;
  font-size: 11px;
  color: #888;
  margin-top: 4px;
  text-align: right;
}

.file-preview {
  max-width: 200px;
  margin-top: 5px;
  border-radius: 8px;
}

.file-link {
  display: inline-block;
  margin-top: 5px;
  color: #007bff;
  text-decoration: underline;
}

.message-form {
  display: flex;
  padding: 10px;
  background: #fff;
  border-top: 1px solid #ddd;
}

.message-input {
  flex: 1;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 20px;
  margin: 0 10px;
  outline: none;
}

.attach-btn {
  cursor: pointer;
  font-size: 20px;
}

.send-btn {
  background: #007bff;
  color: #fff;
  padding: 8px 15px;
  border: none;
  border-radius: 20px;
  cursor: pointer;
}

.no-chat {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  color: gray;
}
